
.middle {
    background-color: #222222;
    width: 100vw;
    height: 100%;
    box-sizing: border-box;
    padding: 20vw 0;

    .content {
        height: 100%;
        overflow-y: scroll;
    }
}
.top {
    background-color: #888888;
    width: 100vw;
    height: 20vw;
    position: fixed;
    left: 0;
    top: 0;
}
.bottom {
    background-color: #888888;
    width: 100vw;
    height: 20vw;
    position: fixed;
    left: 0;
    bottom: 0;
}